<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="dialogFormVisible"
      :modal="false"
      top="17vh"
    >
      <el-form :model="form">
        <el-form-item label="勘察情况" :label-width="formLabelWidth">
          <el-input v-model="form.checkContent" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="解决备注" :label-width="formLabelWidth">
          <el-input v-model="form.solverRemark" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="解决日期" :label-width="formLabelWidth">
          <el-date-picker
            v-model="form.solverDate"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            :clearable="false"
          >
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="workAlarmFun()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { workAlarm } from "@/network/warnMonitor";
export default {
  data() {
    return {
      dialogFormVisible: false,
      form: {
        checkContent: "",
        solverRemark: "",
        solverDate: "",
        alarmId: "",
      },
      formLabelWidth: "120px",
    };
  },
  watch: {
    dialogFormVisible(val) {
      if (val == false) {
        this.form = {
          checkContent: "",
          solverRemark: "",
          solverDate: "",
          alarmId: "",
        };
      }
    },
  },
  mounted() {},
  methods: {
    showDialogFun(alarmId) {
      this.dialogFormVisible = true;
      this.form.alarmId = alarmId;
      this.form.solverDate =
        this.$getDate().year +
        "-" +
        this.$getDate().month +
        "-" +
        this.$getDate().day;
      this.alarmId = alarmId;
    },
    workAlarmFun() {
      workAlarm(
        this.alarmId,
        this.form.checkContent,
        this.form.solverDate,
        this.form.solverRemark
      )
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.$message({
              message: "处理成功!",
              type: "success",
            });
            this.$bus.$emit("workRefsh", true);
            this.dialogFormVisible = false;
          } else {
            this.$message.error(errmsg);
          }
        })
        .catch((err) => {});
    },
  },
};
</script>
<style scoped lang="less">
/deep/ .el-dialog {
  background: #000000de;
  box-shadow: 2px 2px 10px #58585826;
  width: 40%;
}
/deep/ .el-dialog__headerbtn .el-dialog__close {
  color: #fff;
}
.search {
  display: flex;
  > div {
    margin-right: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
    white-space: nowrap;
  }
}
.dialog_content {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
/deep/ .el-form-item__label {
  color: #ffffff;
}
/deep/ .el-select {
  width: 100% !important;
}
/deep/ .el-input__inner,
/deep/ .el-date-editor.el-input {
  width: 100% !important;
}

/deep/ .el-message__icon {
  font-size: 16px !important;
}
</style>