export function energyAlarmAddUpdateDelete(alarmId, dateStr, type) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/energyAlarmAddUpdateDelete",
        params:{
            alarmId:alarmId,
            dateStr:dateStr,
            type:type,
        },
        method: 'POST'
    })
}